import React, {useState} from 'react'


import pic from "./profile-pic.gif";
let name = "Limo";
export default function TextForm(props) {
  
  const handleUpClick = ()=>{
    console.log("Uppercase was clicked");
    let newText = text.toUpperCase();
    setText(newText);
  }
  
  const handleLoClick = ()=>{
    console.log("Lowercase was clicked");
    let newText = text.toLowerCase();
    setText(newText);
  }

  const handleOnChange = (event)=>{
    console.log("On Change");
    setText(event.target.value);
  }
  
  const [count, setCount] = useState(0);
  const [text, setText] = useState("");
  return (
    <>
    <div className="container-fluid row">
      <div className='container col-9'>
          <h1>{props.heading}</h1>
          <div className="form-group">
            <textarea className="form-control" value={text} placeholder="Enter text here" id="mybox" onChange={handleOnChange} rows="5"></textarea>
            <button className="btn btn-primary my-3" onClick={handleUpClick}>Convert to Uppercase</button>
            <button className="btn btn-primary m-3" onClick={handleLoClick}>Convert to Lowercase</button>
            <button className="btn btn-primary" onClick={()=> setText("")}>Clear</button>
          </div>
      </div>
      <div className="container col-3">
        <h3>Your text summary</h3>
        <p>{text.split(" ").length} words, {text.length} characters</p>
      </div>
    </div>
    <hr style={{height:"0.5px" , color: "gray", backgroundColor:"gray"}}/>
    <div className='container-fluid row'>
      <div className="container-fluid col-4 my-4">
        <h1>Click Counter</h1>
        <p>You clicked {count} times.</p>
        <button className="btn btn-primary my-2" onClick={()=> setCount(count + 1)} style={{marginRight: 20}}>Click Me</button>
        <button className="btn btn-primary my-2" onClick={()=> setCount(0)}>Reset Counter</button>
      </div>
      <div className='container-fluid col-4 my-4'>
        <h4>Hello {name}</h4>
        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores consectetur natus dolorem dignissimos accusamus excepturi sint ea optio corporis repellendus, iusto magnam voluptate molestiae similique ratione labore maxime facilis. Inventore. Lorem ipsum dolor sit amet consectetur adipisicing elit. Adipisci a consequuntur velit, eligendi quae aperiam inventore saepe quis ut, eveniet ex! Quos deserunt, aspernatur et ducimus quisquam excepturi? Soluta, nostrum. Lorem ipsum dolor sit amet, consectetur adipisicing elit. Recusandae minima voluptate expedita consectetur praesentium dicta, perspiciatis illo enim assumenda. Cum esse suscipit aliquam ipsum pariatur ipsam. Ab reiciendis vel dolor.</p>
      </div>
      <div className='container-fluid col-4 my-4' style={{float: "right"}}>
        <img src={pic} alt="Profile" height={300} width={300}/>
      </div>
    </div>
    </>
  )
}
